@font-face {
    font-family: 'HelveticaNeueCyr-Roman';
    src: url('../css/fonts/helveticaneuecyr/HelveticaNeueCyr-Roman.woff2') format('woff2'),
        url('../css/fonts/helveticaneuecyr/HelveticaNeueCyr-Roman.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}


@font-face {
    font-family: 'HelveticaNeueCyr-Bold';
    src: url('../css/fonts/helveticaneuecyr/HelveticaNeueCyr-Bold.woff2') format('woff2'),
        url('../css/fonts/helveticaneuecyr/HelveticaNeueCyr-Bold.woff') format('woff');
    font-weight: bold;
    font-style: normal;
}

@font-face {
    font-family: 'HelveticaNeueCyr-Medium';
    src: url('../css/fonts/helveticaneuecyr/HelveticaNeueCyr-Medium.woff2') format('woff2'),
        url('../css/fonts/helveticaneuecyr/HelveticaNeueCyr-Medium.woff') format('woff');
    font-weight: 500;
    font-style: normal;
}